@import url("https://fonts.googleapis.com/css2?family=Inter&display=swap");
.button{
    display: flex;
    font-family: Inter;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 8px 16px;
    gap: 4px;
    border: 0px;
    cursor: pointer;
    font-size: 14px;
    line-height: 20px;
    font-weight: 500;
}
.primary{
    background: #224DFF;
    border-radius: 6px;
    color: #FFFFFF;
}
.secondary{
    background: #FFFFFF ;
    color: #696A6E;
    border: 1px solid #E5E7ED;
    border-radius: 6px;
}
.primary-disabled{
    color: #FFFFFF;
    border-radius: 6px;
    background: #95AAFF;
}